import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../../src/components/common/BaseProps';
import {Content} from '../../src/components/Page';
import PageView from '../components/PageView';
import HighlightCode from '../../src/components/HighlightCode/HighlightCode';

interface ChipApiProps extends BaseProps {
}

export default class ChipApi extends React.PureComponent<ChipApiProps, {}> {
    static defaultProps = {
        prefixCls: 'bm-ChipApi',
    };

    getCode() {
        return `import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../components/common/BaseProps';
import {Content} from '../components/Page';
import Chip, {ChipNode, ChipGroup} from '../components/Chip';
import Avatar from '../components/Avatar';

interface ChipCaseProps extends BaseProps {
}

export default class ChipCase extends React.PureComponent< ChipCaseProps, any> {
    chipData: Array<ChipNode> = [
        {
            label: 'CSS',
            value: 'CSS'
        },
        {
            label: 'Javascript',
            value: 'Javascript'
        }
    ];
    handleDelete = (event: any) => {
        console.log(event);
    };
    render() {
        const {className, ...other} = this.props;
        const styleClass = classNames(
            'ChipCase', className
        );
        return (
            <Content className={styleClass} {...other}>
                <div className="CasePanel">
                    <Chip active={true} label="Javascript"/>
                    <Chip avatar={
                        <Avatar src="assets/images/users/1.jpg"/>
                    } label="Javascript"/>
                    <Chip avatar={
                        <Avatar src="assets/images/users/1.jpg"/>
                    } label="Javascript" onDelete={this.handleDelete}/>
                </div>
                <div className="CasePanel">
                    <ChipGroup data={this.chipData} placeholder="Love"/>
                </div>
            </Content>
        );
    }
}`;
    }

    getFields() {
        return [{
            field: 'attr',
            header: '',
        }, {
            field: 'desc',
            header: '',
        }, {
            field: 'type',
            header: '',
        }, {
            field: 'default',
            header: '',
        }];
    }

    render() {
        const {className, prefixCls} = this.props;
        const styleClass = classNames(
            prefixCls, className,
            'ApiContent',
        );
        const data = [{
            name: 'Chip',
            fields: this.getFields(),
            attributes: [{
                attr: 'avatar',
                desc: '',
                type: 'number',
                default: '-',
            }, {
                attr: 'active',
                desc: '',
                type: 'boolean',
                default: 'false',
            }, {
                attr: 'label',
                desc: '',
                type: 'React.ReactNode | String',
                default: '-',
            }, {
                attr: 'close',
                desc: '',
                type: 'boolean',
                default: 'false',
            }, {
                attr: 'value',
                desc: '',
                type: 'any',
                default: '-',
            }, {
                attr: 'onClick',
                desc: 'click',
                type: '({label, value}) => void',
                default: '-',
            }, {
                attr: 'onDelete',
                desc: '',
                type: '({label, value}) => void',
                default: '-',
            }],
        }, {
            name: 'ChipGroup',
            fields: this.getFields(),
            attributes: [{
                attr: 'data',
                desc: '',
                type: 'Array< ChipNode>',
                default: '-',
            }, {
                attr: 'placeholder',
                desc: '',
                type: 'String',
                default: '-',
            }, {
                attr: 'onClick',
                desc: 'click',
                type: '({label, value}) => void',
                default: '-',
            }, {
                attr: 'onDelete',
                desc: '',
                type: '({label, value}) => void',
                default: '-',
            }],
        }];
        const code = `export interface ChipNode {
    avatar?: React.ReactNode;
    label?: string;
    value?: any;
}`;
        return (
            <Content className={styleClass}>
                <PageView
                    title="Chip "
                    code={this.getCode()}
                    data={data}
                    app="WEB"
                    frameUrl="#/components/chip"
                    description=""
                />
                <div className="padding-left-20 padding-right-20">
                    <h3>ChipNode</h3>
                    <p></p>
                    <HighlightCode lang="typescript">
                        {code}
                    </HighlightCode>
                </div>
            </Content>
        );
    }
}
