import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../../src/components/common/BaseProps';
import {Content} from '../../src/components/Page';
import PageView from '../components/PageView';

interface LockerApiProps extends BaseProps {
}

export default class LockerApi extends React.PureComponent<LockerApiProps, {}> {
    static defaultProps = {
        prefixCls: 'bm-LockerApi',
    };

    getCode() {
        return `import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../components/common/BaseProps';
import Locker from '../components/Locker';
import {Content} from '../components/Page';

interface LockerCaseProps extends BaseProps {
}

interface LockerCaseState {
    message?: string;
}

export default class LockerCase extends React.PureComponent< LockerCaseProps, LockerCaseState> {
    static defaultProps = {
        prefixCls: 'bm-LockerCase'
    };

    state = {
        message: ''
    };
    record: any = [];

    handleDone = (event: any) => {
        let rs = event;
        if (rs.points.length < 4) {
            this.setState({
                message: ''
            });
            this.record = [];
            rs.clear();
            return;
        }
        this.record.push(rs.points.join(''));
        if (this.record.length >= 2) {
            if (this.record[0] == this.record[1]) {
                this.setState({
                    message: ''
                });
            } else {
                this.setState({
                    message: ''
                });
            }
            rs.clear();
            this.record = [];
        } else {
            this.setState({
                message: ''
            });
            rs.clear();
        }
    };

    render() {
        const {className, prefixCls} = this.props;
        const styleClass = classNames(
            prefixCls, className
        );
        return (
            <Content className={styleClass}>
                <div className="CasePanel">
                    <Locker onDone={this.handleDone}/>
                    <div className="Locker-Remind">{this.state.message}</div>
                </div>
            </Content>
        );
    }
}`;
    }

    getFields() {
        return [{
            field: 'attr',
            header: '',
        }, {
            field: 'desc',
            header: '',
        }, {
            field: 'type',
            header: '',
        }, {
            field: 'default',
            header: '',
        }];
    }

    render() {
        const {className, prefixCls} = this.props;
        const styleClass = classNames(
            prefixCls, className,
            'ApiContent',
        );
        const data = [{
            name: 'Locker',
            fields: this.getFields(),
            attributes: [{
                attr: 'width',
                desc: '',
                type: 'Number | String',
                default: '300',
            }, {
                attr: 'height',
                desc: '',
                type: 'Number | String',
                default: '300',
            }, {
                attr: 'strokeStyle',
                desc: '',
                type: 'String',
                default: 'rgba(0,0,0,.12)',
            }, {
                attr: 'fillStyle',
                desc: '',
                type: 'String',
                default: '#2196f3',
            }, {
                attr: 'onDone',
                desc: '',
                type: '({points, clear}) => {}',
                default: '-',
            }],
        }];
        return (
            <Content className={styleClass}>
                <PageView
                    title="Locker "
                    code={this.getCode()}
                    data={data}
                    app="WEB"
                    frameUrl="#/components/locker"
                    description=""
                />
            </Content>
        );
    }
}
