import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../../src/components/common/BaseProps';
import {Content} from '../../src/components/Page';
import PageView from '../components/PageView';

interface KeyboardApiProps extends BaseProps {
}

export default class KeyboardApi extends React.PureComponent<KeyboardApiProps, {}> {
    static defaultProps = {
        prefixCls: 'bm-KeyboardApi',
    };

    getCode() {
        return `import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../components/common/BaseProps';
import Button from '../components/Button';
import {Content} from '../components/Page';
import ActionSheet from '../components/ActionSheet';
import KeyBoard from '../components/KeyBoard';

interface KeyBoardCaseProps extends BaseProps {
}

interface KeyBoardCaseState {
    password?: string;
}

export default class KeyBoardCase extends React.Component< KeyBoardCaseProps, KeyBoardCaseState> {
    static defaultProps: KeyBoardCaseProps = {
        prefixCls: 'bm-KeyBoardCase'
    };
    password: string = '12345';

    handleFinish = (value: string) => {
        if (this.password === value) {
            console.log(1);
        }
    };

    handleClose = () => {
        ActionSheet.close();
    };

    handleClick = () => {
        ActionSheet.show({
            actionClass: 'bm-KeyBoard-group',
            element: (<KeyBoard action={<a href=""></a>} random={true} onClose={this.handleClose} onFinish={this.handleFinish}/>),
            onClose: () => {
                console.log(2);
            }
        })
    };

    render() {
        const {className, prefixCls} = this.props;
        const styleClass = classNames(
            prefixCls, className
        );
        return (
            <Content className={styleClass}>
                <div className="CasePanel">
                    <Button onClick={this.handleClick}></Button>
                </div>
            </Content>
        );
    }
}`;
    }

    getFields() {
        return [{
            field: 'attr',
            header: '',
        }, {
            field: 'desc',
            header: '',
        }, {
            field: 'type',
            header: '',
        }, {
            field: 'default',
            header: '',
        }];
    }

    render() {
        const {className, prefixCls} = this.props;
        const styleClass = classNames(
            prefixCls, className,
            'ApiContent',
        );
        const data = [{
            name: 'KeyBoard',
            fields: this.getFields(),
            attributes: [{
                attr: 'action',
                desc: 'es',
                type: 'React.ReactNode',
                default: '-',
            }, {
                attr: 'count',
                desc: '',
                type: 'Number',
                default: '5',
            }, {
                attr: 'random',
                desc: '',
                type: 'Boolean',
                default: 'false',
            }, {
                attr: 'title',
                desc: '',
                type: 'React.ReactNode',
                default: '',
            }, {
                attr: 'onClose',
                desc: '',
                type: 'Function',
                default: '-',
            }, {
                attr: 'onFinish',
                desc: '',
                type: '(value: string): void',
                default: '-',
            }],
        }];
        return (
            <Content className={styleClass}>
                <PageView
                    title="KeyBoard "
                    code={this.getCode()}
                    data={data}
                    app="WEB"
                    frameUrl="#/components/keyboard"
                    description=""
                />
            </Content>
        );
    }
}
