import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../../src/components/common/BaseProps';
import {Content} from '../../src/components/Page';
import PageView from '../components/PageView';

interface ToastApiProps extends BaseProps {
}

export default class ToastApi extends React.PureComponent<ToastApiProps, {}> {
    static defaultProps = {
        prefixCls: 'bm-ToastApi',
    };

    getCode() {
        return `import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../components/common/BaseProps';
import Button from '../components/Button';
import Toast from '../components/Toast';
import {Content} from '../components/Page';

interface ToastCaseProps extends BaseProps {
    messages?: Array< object>
}

interface ToastCaseState {
    messages: Array< object>
}

export default class ToastCase extends React.PureComponent<ToastCaseProps, ToastCaseState> {
    state: ToastCaseState = {
        messages: []
    };
    handleClick = () => {
        Toast.show({
            message: ''
        });
    };

    render() {
        const {className, ...other} = this.props;
        const styleClass = classNames(
            'ToastCase',
            className
        );
        return (
            <Content className={styleClass} {...other}>
                <div className="CasePanel">
                    <Button onClick={this.handleClick}> Toast</Button>
                </div>
            </Content>
        );
    }
}`;
    }

    getFields() {
        return [{
            field: 'attr',
            header: '',
        }, {
            field: 'desc',
            header: '',
        }, {
            field: 'type',
            header: '',
        }, {
            field: 'default',
            header: '',
        }];
    }

    render() {
        const {className, prefixCls} = this.props;
        const styleClass = classNames(
            prefixCls, className,
            'ApiContent',
        );
        const data = [{
            name: 'Toast.show()',
            fields: this.getFields(),
            attributes: [{
                attr: 'avatar',
                desc: '',
                type: 'React.ReactNode',
                default: '-',
            }, {
                attr: 'delay',
                desc: '0',
                type: 'Number',
                default: '2500',
            }, {
                attr: 'message',
                desc: '',
                type: 'React.ReactNode | string',
                default: '-',
            }, {
                attr: 'position',
                desc: ' top | center | bottom',
                type: 'String',
                default: 'center',
            }, {
                attr: 'type',
                desc: 'info | success | warning | error',
                type: 'String',
                default: '-',
            }, {
                attr: 'onClose',
                desc: '',
                type: 'Function',
                default: '-',
            }],
        }];
        return (
            <Content className={styleClass}>
                <PageView
                    title="Toast "
                    code={this.getCode()}
                    data={data}
                    app="WEB"
                    frameUrl="#/components/toast"
                    description=""
                />
                <div className="padding-left-20 padding-right-20">
                    <h3>Toast.info()</h3>
                    <p>info</p>
                </div>
                <div className="padding-left-20 padding-right-20">
                    <h3>Toast.success()</h3>
                    <p>success</p>
                </div>
                <div className="padding-left-20 padding-right-20">
                    <h3>Toast.warning()</h3>
                    <p>warning</p>
                </div>
                <div className="padding-left-20 padding-right-20">
                    <h3>Toast.error()</h3>
                    <p>error</p>
                </div>
                <div className="padding-left-20 padding-right-20">
                    <h3>Toast.close()</h3>
                    <p></p>
                </div>
            </Content>
        );
    }
}
