import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../../src/components/common/BaseProps';
import {Content} from '../../src/components/Page';
import PageView from '../components/PageView';

interface IndicatorApiProps extends BaseProps {
}

export default class IndicatorApi extends React.PureComponent<IndicatorApiProps, {}> {
    static defaultProps = {
        prefixCls: 'bm-IndicatorApi',
    };

    getCode() {
        return `import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../components/common/BaseProps';
import Button from '../components/Button';
import Indicator from '../components/Indicator';
import {Content} from '../components/Page';

interface IndicatorCaseProps extends BaseProps {
}

interface IndicatorCaseState {
    visible: boolean;
}

export default class IndicatorCase extends React.PureComponent< IndicatorCaseProps, IndicatorCaseState> {
    state: IndicatorCaseState = {
        visible: false
    };

    openIndicator = () => {
        Indicator.show();
    };

    openSpinner = () => {
        Indicator.show({type: 'circleLine'});
    };

    openIndicatorWithText = () => {
        Indicator.show({message: '...'});
    };

    render() {
        const {className} = this.props;
        const styleClass = classNames(
            'IndicatorCase',
            className
        );
        return (
            <Content className={styleClass}>
                <section className="CasePanel">
                    <Button block onClick={this.openIndicator}> Indicator</Button>
                    <Button block onClick={this.openSpinner}> spinner</Button>
                    <Button block onClick={this.openIndicatorWithText}> Indicator</Button>
                </section>
            </Content>
        );
    }
}`;
    }

    getFields() {
        return [{
            field: 'attr',
            header: '',
        }, {
            field: 'desc',
            header: '',
        }, {
            field: 'type',
            header: '',
        }, {
            field: 'default',
            header: '',
        }];
    }

    render() {
        const {className, prefixCls} = this.props;
        const styleClass = classNames(
            prefixCls, className,
            'ApiContent',
        );
        const data = [{
            name: 'Indicator.show()',
            fields: this.getFields(),
            attributes: [{
                attr: 'delay',
                desc: '',
                type: 'Number',
                default: '2500',
            }, {
                attr: 'message',
                desc: '',
                type: 'React.ReactNode | string',
                default: '-',
            }, {
                attr: 'size',
                desc: 'spinlg | sm | xs',
                type: 'String',
                default: 'xs',
            }, {
                attr: 'type',
                desc: `spin'circleRound'
                | 'circleLine'
                | 'circleRoundFade'
                | 'lineSquare'
                | 'lineRound'
                | 'lineBounce'
                | 'snake'`,
                type: 'String',
                default: 'circleRound',
            }, {
                attr: 'onClose',
                desc: '',
                type: 'Function',
                default: '-',
            }],
        }];
        return (
            <Content className={styleClass}>
                <PageView
                    title="Indicator "
                    code={this.getCode()}
                    data={data}
                    app="WEB"
                    frameUrl="#/components/indicator"
                    description=" "
                />
                <div className="padding-left-20 padding-right-20">
                    <h3>Indicator.close()</h3>
                </div>
            </Content>
        );
    }
}
