import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../../src/components/common/BaseProps';
import {Content} from '../../src/components/Page';
import PageView from '../components/PageView';

interface TreeApiProps extends BaseProps {
}

export default class TreeApi extends React.PureComponent<TreeApiProps, {}> {
    static defaultProps = {
        prefixCls: 'bm-TreeApi',
    };

    getCode() {
        return `import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps&} from '../components/common/BaseProps';
import ActionSheet from '../components/ActionSheet'
import {Content&} from '../components/Page';
import Button from '../components/Button';

interface ActionSheetCaseProps extends BaseProps {}

export default class ActionSheetCase extends React.PureComponent< ActionSheetCaseProps, {}> {

    handleClick = () => {
        ActionSheet.show({
            buttons: [{
                text: 'Button1'
            }, {
                text: 'Button2'
            }],
            cancel: true
        })
    };

    render() {
        const {className} = this.props;
        const styleClass = classNames(
             'ActionSheetCase', className
        );
        return (
            <Content className={styleClass}>
                <div className="CasePanel">
                    <Button theme="primary" onClick={this.handleClick}>Show Action Sheet</Button>
                </div>
            </Content>
        );
    }
}`;
    }

    getFields() {
        return [{
            field: 'attr',
            header: '',
        }, {
            field: 'desc',
            header: '',
        }, {
            field: 'type',
            header: '',
        }, {
            field: 'default',
            header: '',
        }];
    }

    render() {
        const {className, prefixCls} = this.props;
        const styleClass = classNames(
            prefixCls, className,
            'ApiContent',
        );
        const data = [{
            name: 'Badge',
            fields: this.getFields(),
            attributes: [{
                attr: 'defaultValue',
                desc: '',
                type: 'Date | string',
                default: '',
            }, {
                attr: 'minDate',
                desc: '',
                type: 'Date | string',
                default: '2000/1/1',
            }, {
                attr: 'maxDate',
                desc: '',
                type: 'Date | string',
                default: '2020/1/1',
            }, {
                attr: 'visible',
                desc: '',
                type: 'Boolean',
                default: 'false',
            }, {
                attr: 'onOk',
                desc: '',
                type: '(value: string) => void',
                default: 'noop',
            }],
        }];
        return (
            <Content className={styleClass}>
                <PageView
                    title="Tree "
                    code={this.getCode()}
                    data={data}
                    app="WEB"
                    frameUrl="#/components/tree"
                    description=""
                />
            </Content>
        );
    }
}
