import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../../src/components/common/BaseProps';
import {Content} from '../../src/components/Page';
import PageView from '../components/PageView';

interface ModalApiProps extends BaseProps {
}

export default class ModalApi extends React.PureComponent<ModalApiProps, {}> {
    static defaultProps = {
        prefixCls: 'bm-ModalApi',
    };

    getCode() {
        return `import * as React from 'react';
import * as classNames from 'classnames';
import Modal from '../components/Modal';
import Button from '../components/Button';
import {BaseProps} from '../components/common/BaseProps';
import {Content} from '../components/Page';

interface Props extends BaseProps {
}

interface State {
    visible: boolean
}

export default class ModalCase extends React.PureComponent< Props, State> {
    state = {
        visible: false
    };

    handleOpen = () => {
        this.setState({
            visible: true
        });
    }

    handleClose = () => {
        this.setState({
            visible: false
        });
    };

    render() {
        const styleClass = classNames(
            'ModalCase'
        );
        return (
            <Content className={styleClass}>
                <div className="CasePanel">
                    <Button theme="primary" onClick={this.handleOpen}>Open Modal</Button>
                    <Modal header="" visible={this.state.visible} onClose={this.handleClose}>
                        <div className="padding-10">
                            Modal
                        </div>
                    </Modal>
                </div>
            </Content>
        );
    }
}`;
    }

    getFields() {
        return [{
            field: 'attr',
            header: '',
        }, {
            field: 'desc',
            header: '',
        }, {
            field: 'type',
            header: '',
        }, {
            field: 'default',
            header: '',
        }];
    }

    render() {
        const {className, prefixCls} = this.props;
        const styleClass = classNames(
            prefixCls, className,
            'ApiContent',
        );
        const data = [{
            name: 'Modal',
            fields: this.getFields(),
            attributes: [{
                attr: 'header',
                desc: 'header',
                type: 'React.ReactNode | String',
                default: '-',
            }, {
                attr: 'hideBackdrop',
                desc: '',
                type: 'Boolean',
                default: 'false',
            }, {
                attr: 'maxHeight',
                desc: '',
                type: 'Number | String',
                default: '-',
            }, {
                attr: 'visible',
                desc: '',
                type: 'Boolean',
                default: 'false',
            }, {
                attr: 'onClose',
                desc: '',
                type: 'Function',
                default: '-',
            }, {
                attr: 'onOpen',
                desc: '',
                type: 'Function',
                default: '-',
            }],
        }];
        return (
            <Content className={styleClass}>
                <PageView
                    title="Modal "
                    code={this.getCode()}
                    data={data}
                    app="WEB"
                    frameUrl="#/components/modal"
                    description=""
                />
            </Content>
        );
    }
}
