import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../../src/components/common/BaseProps';
import {Content} from '../../src/components/Page';
import PageView from '../components/PageView';

interface DatetimePickerApiProps extends BaseProps {
}

export default class DatetimePickerApi extends React.PureComponent<DatetimePickerApiProps, {}> {
    static defaultProps = {
        prefixCls: 'bm-DatetimePickerApi',
    };

    getCode() {
        return `import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../../src/components/common/BaseProps';
import DatetimePicker from '../../src/components/DatetimePicker';
import {Content} from '../../src/components/Page';

interface DatetimePickerCaseProps extends BaseProps {
}

interface DatetimePickerCaseState {
    visible?: boolean;
}

export default class DatetimePickerCase extends React.PureComponent< DatetimePickerCaseProps, DatetimePickerCaseState> {
    state: DatetimePickerCaseState = {
        visible: false,
    };

    handleClose = () => {
        this.setState({
            visible: false,
        });
    }

    handleChange = (event: any) => {
        const ev = event;
    }

    handleOpen = () => {
        this.setState({
            visible: true,
        });
    }

    render() {
        const {className} = this.props;
        const styleClass = classNames(
            'DatetimePickerCase',
            className,
        );
        const {visible} = this.state;
        return (
            <Content className={styleClass}>
                <section className="CasePanel">
                    <DatetimePicker
                        visible={visible}
                        minDate="2010/1/1"
                        onOpen={this.handleOpen}
                        onOk={this.handleChange}
                        onClose={this.handleClose}
                    />
                </section>
            </Content>
        );
    }
}
`;
    }

    getFields() {
        return [{
            field: 'attr',
            header: '',
        }, {
            field: 'desc',
            header: '',
        }, {
            field: 'type',
            header: '',
        }, {
            field: 'default',
            header: '',
        }];
    }

    render() {
        const {className, prefixCls} = this.props;
        const styleClass = classNames(
            prefixCls, className,
            'ApiContent',
        );
        const data = [{
            name: 'Badge',
            fields: this.getFields(),
            attributes: [{
                attr: 'defaultValue',
                desc: '',
                type: 'Date | string',
                default: '',
            }, {
                attr: 'minDate',
                desc: '',
                type: 'Date | string',
                default: '2000/1/1',
            }, {
                attr: 'maxDate',
                desc: '',
                type: 'Date | string',
                default: '2020/1/1',
            }, {
                attr: 'visible',
                desc: '',
                type: 'Boolean',
                default: 'false',
            }, {
                attr: 'onOk',
                desc: '',
                type: '(value: string) => void',
                default: 'noop',
            }],
        }];
        return (
            <Content className={styleClass}>
                <PageView
                    title="DatetimePicker "
                    code={this.getCode()}
                    data={data}
                    app="WEB"
                    frameUrl="#/components/datetimePicker"
                    description=""
                />
            </Content>
        );
    }
}
