import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../../src/components/common/BaseProps';
import Button from '../../src/components/Button';
import Indicator from '../../src/components/Indicator';
import {Content} from '../../src/components/Page';

interface IndicatorCaseProps extends BaseProps {
}

interface IndicatorCaseState {
    visible: boolean;
}

export default class IndicatorCase extends React.PureComponent<IndicatorCaseProps, IndicatorCaseState> {
    state: IndicatorCaseState = {
        visible: false,
    };

    openIndicator = () => {
        Indicator.show();
    }

    openSpinner = () => {
        Indicator.show({type: 'circleLine'});
    }

    openIndicatorWithText = () => {
        Indicator.show({message: '...'});
    }

    render() {
        const {className} = this.props;
        const styleClass = classNames(
            'IndicatorCase',
            className,
        );
        return (
            <Content className={styleClass}>
                <section className="CasePanel">
                    <Button block={true} onClick={this.openIndicator}> Indicator</Button>
                    <Button block={true} onClick={this.openSpinner}> spinner</Button>
                    <Button block={true} onClick={this.openIndicatorWithText}> Indicator</Button>
                </section>
            </Content>
        );
    }
}
