import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../../src/components/common/BaseProps';
import {Content} from '../../src/components/Page';
import PageView from '../components/PageView';

interface BackTopApiProps extends BaseProps {
}

export default class BackTopApi extends React.PureComponent<BackTopApiProps, {}> {
    static defaultProps = {
        prefixCls: 'bm-BackTopApi',
    };

    getCode() {
        return `import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../components/common/BaseProps';
import {Content} from '../components/Page';
import BackTop from '../components/BackTop';
import Icon from '../components/Icon';
import Button from '../components/Button';

interface BackTopCaseProps extends BaseProps {
}

export default class BackTopCase extends React.PureComponent< BackTopCaseProps, any> {
    scrollNode: any;

    getRef = (node: any) => {
        this.scrollNode = node;
    };

    render() {
        const {className, ...other} = this.props;
        const styleClass = classNames(
            'BackTopCase',
            className
        );
        return (
            <Content className={styleClass} {...other} ref={this.getRef}>
                
                <div className="scrollBox">
                    Content
                    <BackTop>
                        <Button theme="primary" shape="circle" size="lg">
                            <Icon icon="keyboard_arrow_up"/>
                        </Button>
                    </BackTop>
                </div>
            </Content>
        );
    }
}`;
    }

    getFields() {
        return [{
            field: 'attr',
            header: '',
        }, {
            field: 'desc',
            header: '',
        }, {
            field: 'type',
            header: '',
        }, {
            field: 'default',
            header: '',
        }];
    }

    render() {
        const {className, prefixCls} = this.props;
        const styleClass = classNames(
            prefixCls, className,
            'ApiContent',
        );
        const data = [{
            name: 'BackTop',
            fields: this.getFields(),
            attributes: [{
                attr: 'destination',
                desc: '',
                type: 'Number',
                default: '0',
            }, {
                attr: 'duration',
                desc: '',
                type: 'Number',
                default: '200',
            }, {
                attr: 'easing',
                desc: '',
                type: 'String',
                default: 'linear',
            }, {
                attr: 'scrollNode',
                desc: '',
                type: 'React.ReactNode',
                default: '-',
            }, {
                attr: 'onScrollEnd',
                desc: '',
                type: 'Function',
                default: '-',
            }],
        }];
        return (
            <Content className={styleClass}>
                <PageView
                    title="BackTop "
                    code={this.getCode()}
                    data={data}
                    app="WEB"
                    frameUrl="#/components/backTop"
                    description=""
                />
            </Content>
        );
    }
}
