import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../../src/components/common/BaseProps';
import {Content} from '../../src/components/Page';
import PageView from '../components/PageView';

interface TooltipApiProps extends BaseProps {
}

export default class TooltipApi extends React.PureComponent<TooltipApiProps, {}> {
    static defaultProps = {
        prefixCls: 'bm-TooltipApi',
    };

    getCode() {
        return `import * as React from 'react';
import * as classNames from 'classnames';
import Tooltip from '../components/Tooltip';
import Button from '../components/Button';
import {BaseProps} from '../components/common/BaseProps';
import {Content} from '../components/Page';

interface TooltipCaseProp extends BaseProps {
}

export default class TooltipCase extends React.PureComponent< TooltipCaseProp, {}> {
    render() {
        const {className} = this.props;
        const styleClass = classNames(
            'TooltipCase',
            className
        );
        return (
            <Content className={styleClass}>
                <div className="CasePanel">
                    <Tooltip title="">
                        <Button theme="primary">Bottom</Button>
                    </Tooltip>

                    <Tooltip title="" dir="right">
                        <Button theme="primary">Right</Button>
                    </Tooltip>


                    <Tooltip title="" dir="left">
                        <Button theme="primary">Left</Button>
                    </Tooltip>


                    <Tooltip title="" dir="top">
                        <Button theme="primary">Top</Button>
                    </Tooltip>
                </div>
            </Content>
        );
    }
}`;
    }

    getFields() {
        return [{
            field: 'attr',
            header: '',
        }, {
            field: 'desc',
            header: '',
        }, {
            field: 'type',
            header: '',
        }, {
            field: 'default',
            header: '',
        }];
    }

    render() {
        const {className, prefixCls} = this.props;
        const styleClass = classNames(
            prefixCls, className,
            'ApiContent',
        );
        const data = [{
            name: 'Tooltip',
            fields: this.getFields(),
            attributes: [{
                attr: 'delay',
                desc: '',
                type: 'Number',
                default: '1000',
            }, {
                attr: 'dir',
                desc: 'top | right | bottom | left',
                type: 'String',
                default: 'bottom',
            }, {
                attr: 'title',
                desc: '',
                type: 'String',
                default: '-',
            }, {
                attr: 'onOpen',
                desc: '',
                type: 'Function',
                default: '-',
            }, {
                attr: 'onClose',
                desc: '',
                type: 'Function',
                default: '-',
            }],
        }];
        return (
            <Content className={styleClass}>
                <PageView
                    title="Tooltip "
                    code={this.getCode()}
                    data={data}
                    app="WEB"
                    frameUrl="#/components/tooltip"
                    description=""
                />
            </Content>
        );
    }
}
