import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../../src/components/common/BaseProps';
import {Content} from '../../src/components/Page';
import PageView from '../components/PageView';

interface ImageViewApiProps extends BaseProps {
}

export default class ImageViewApi extends React.PureComponent<ImageViewApiProps, {}> {
    static defaultProps = {
        prefixCls: 'bm-ImageViewApi',
    };

    getCode() {
        return `import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../components/common/BaseProps';
import ImageView from '../components/ImageView';
import Button from '../components/Button';
import {Content} from '../components/Page';

interface ImageViewCaseProps extends BaseProps {
}

export default class ImageViewCase extends React.PureComponent< ImageViewCaseProps, any> {
    state = {
        visible: false
    };

    viewNode: any;

    getRef = (node: any) => {
        this.viewNode = node;
    };

    open = () => {
        this.setState({
            visible: true
        });
    };

    handleClose = () => {
        this.setState({
            visible: false
        });
    };

    render() {
        const images = [
            {
                src: 'assets/images/lists/image1.jpg'
            }, {
                src: 'assets/images/lists/image2.jpg'
            }
        ];
        const {className, ...other} = this.props;
        const styleClass = classNames(
            'ImageViewCase',
            className
        );
        return (
            <Content className={styleClass} {...other}>
                <section className="CasePanel" ref={this.getRef}>
                    <Button onClick={this.open}></Button>
                    {
                        images.map((img: any, index: number) => {
                            return <img src={img.src} key={index}/>;
                        })
                    }
                </section>
                <ImageView visible={this.state.visible!} target={this.viewNode}
                           onClose={this.handleClose}/>
            </Content>
        );
    }
}`;
    }

    getFields() {
        return [{
            field: 'attr',
            header: '',
        }, {
            field: 'desc',
            header: '',
        }, {
            field: 'type',
            header: '',
        }, {
            field: 'default',
            header: '',
        }];
    }

    render() {
        const {className, prefixCls} = this.props;
        const styleClass = classNames(
            prefixCls, className,
            'ApiContent',
        );
        const data = [{
            name: 'ImageView',
            fields: this.getFields(),
            attributes: [{
                attr: 'target',
                desc: '',
                type: 'React.ReactNode',
                default: 'document.body',
            }, {
                attr: 'visible',
                desc: '',
                type: 'Boolean',
                default: 'false',
            }],
        }];
        return (
            <Content className={styleClass}>
                <PageView
                    title="ImageView "
                    code={this.getCode()}
                    data={data}
                    app="WEB"
                    frameUrl="#/components/imageView"
                    description=""
                />
            </Content>
        );
    }
}
