import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../../src/components/common/BaseProps';
import {Content} from '../../src/components/Page';
import PageView from '../components/PageView';

interface LazyloadApiProps extends BaseProps {
}

export default class LazyloadApi extends React.PureComponent<LazyloadApiProps, {}> {
    static defaultProps = {
        prefixCls: 'bm-LazyloadApi',
    };

    getCode() {
        return `import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../../src/components/common/BaseProps';
import {Content} from '../../src/components/Page';
import LazyLoad from '../../src/components/LazyLoad';

interface LazyLoadCaseProps extends BaseProps {
}

export default class LazyLoadCase extends React.PureComponent< LazyLoadCaseProps, {}> {
    static defaultProps: LazyLoadCaseProps = {
        prefixCls: 'bm-LazyLoadCase'
    };

    render() {
        const {className, prefixCls} = this.props;
        const styleClass = classNames(
            prefixCls, className
        );
        return (
            <Content className={styleClass}>
                <LazyLoad height={200}>
                    <img src="assets/images/lists/image1.jpg" style={{width: '100%'}} alt=""/>
                </LazyLoad>
                <div className="padding-10">
                    Bee MobileReact
                    
                </div>
                <LazyLoad height={200}>
                    <img src="assets/images/lists/image2.jpg" style={{width: '100%'}} alt=""/>
                </LazyLoad>
                <div className="padding-10">
                    Bee MobileReact
                    
                </div>
                <LazyLoad height={200}>
                    <img src="assets/images/lists/image3.jpg" style={{width: '100%'}} alt=""/>
                </LazyLoad>
                <LazyLoad height={200}>
                    <img src="assets/images/lists/image4.jpg" style={{width: '100%'}} alt=""/>
                </LazyLoad>
            </Content>
        );
    }
}`;
    }

    getFields() {
        return [{
            field: 'attr',
            header: '',
        }, {
            field: 'desc',
            header: '',
        }, {
            field: 'type',
            header: '',
        }, {
            field: 'default',
            header: '',
        }];
    }

    render() {
        const {className, prefixCls} = this.props;
        const styleClass = classNames(
            prefixCls, className,
            'ApiContent',
        );
        const data = [{
            name: 'LazyLoad',
            fields: this.getFields(),
            attributes: [{
                attr: 'debounce',
                desc: '.',
                type: 'Number',
                default: '300',
            }, {
                attr: 'height',
                desc: '',
                type: 'Number',
                default: '-',
            }, {
                attr: 'placeholder',
                desc: '',
                type: 'number',
                default: '-',
            }, {
                attr: 'scrollNode',
                desc: '',
                type: 'React.ReactNode',
                default: 'document.body',
            }, {
                attr: 'throttle',
                desc: 'scroll',
                type: 'Number',
                default: '300',
            }, {
                attr: 'width',
                desc: '',
                type: 'Number',
                default: '-',
            }],
        }];
        return (
            <Content className={styleClass}>
                <PageView
                    title="LazyLoad "
                    code={this.getCode()}
                    data={data}
                    app="WEB"
                    frameUrl="#/components/lazyload"
                    description=""
                />
            </Content>
        );
    }
}
