import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../../src/components/common/BaseProps';
import {Content} from '../../src/components/Page';
import PageView from '../components/PageView';

interface SpinApiProps extends BaseProps {
}

export default class SpinApi extends React.PureComponent<SpinApiProps, {}> {
    static defaultProps = {
        prefixCls: 'bm-SpinApi',
    };

    getCode() {
        return `import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../components/common/BaseProps';
import Spin from '../components/Spin';
import List, {ListItem, ListItemText, ListItemAction} from '../components/List';
import {Content} from '../components/Page';

interface SpinCaseProps extends BaseProps {
}

export default class SpinCase extends React.PureComponent< SpinCaseProps, any> {
    spinners: any[] = ['circleRound' ,'circleLine' ,'circleRoundFade' ,'lineSquare' ,'lineRound' ,'lineBounce', 'snake'];
    render() {
        const {className, ...other} = this.props;
        const styleClass = classNames(
            'SpinCase',
            className
        );
        return (
            <Content className={styleClass} {...other}>
                <div className="CasePanel">
                    <List>
                        {
                            this.spinners.map((spinner, index) => (
                                <ListItem key={index.toString()}>
                                    <ListItemText>
                                        {spinner}
                                    </ListItemText>
                                    <ListItemAction>
                                        <Spin type={spinner}/>
                                    </ListItemAction>
                                </ListItem>
                            ))
                        }
                    </List>
                </div>
            </Content>
        );
    }
}`;
    }

    getFields() {
        return [{
            field: 'attr',
            header: '',
        }, {
            field: 'desc',
            header: '',
        }, {
            field: 'type',
            header: '',
        }, {
            field: 'default',
            header: '',
        }];
    }

    render() {
        const {className, prefixCls} = this.props;
        const styleClass = classNames(
            prefixCls, className,
            'ApiContent',
        );
        const data = [{
            name: 'Spin',
            fields: this.getFields(),
            attributes: [{
                attr: 'size',
                desc: 'lg | sm | xs',
                type: 'String',
                default: 'xs',
            }, {
                attr: 'type',
                desc: `'circleRound'
                | 'circleLine'
                | 'circleRoundFade'
                | 'lineSquare'
                | 'lineRound'
                | 'lineBounce'
                | 'snake'`,
                type: 'String',
                default: 'circleRound',
            }],
        }];
        return (
            <Content className={styleClass}>
                <PageView
                    title="Spin "
                    code={this.getCode()}
                    data={data}
                    app="WEB"
                    frameUrl="#/components/spin"
                    description=""
                />
            </Content>
        );
    }
}
