import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../../src/components/common/BaseProps';
import {Content} from '../../src/components/Page';
import PageView from '../components/PageView';

interface FabButtonApiProps extends BaseProps {
}

export default class FabButtonApi extends React.PureComponent<FabButtonApiProps, {}> {
    static defaultProps = {
        prefixCls: 'bm-FabButtonApi',
    };

    getCode() {
        return `import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps&} from '../components/common/BaseProps';
import ActionSheet from '../components/ActionSheet'
import {Content&} from '../components/Page';
import Button from '../components/Button';

interface ActionSheetCaseProps extends BaseProps {}

export default class ActionSheetCase extends React.PureComponent< ActionSheetCaseProps, {}> {

    handleClick = () => {
        ActionSheet.show({
            buttons: [{
                text: 'Button1'
            }, {
                text: 'Button2'
            }],
            cancel: true
        })
    };

    render() {
        const {className} = this.props;
        const styleClass = classNames(
             'ActionSheetCase', className
        );
        return (
            <Content className={styleClass}>
                <div className="CasePanel">
                    <Button theme="primary" onClick={this.handleClick}>Show Action Sheet</Button>
                </div>
            </Content>
        );
    }
}`;
    }

    getFields() {
        return [{
            field: 'attr',
            header: '',
        }, {
            field: 'desc',
            header: '',
        }, {
            field: 'type',
            header: '',
        }, {
            field: 'default',
            header: '',
        }];
    }

    render() {
        const {className, prefixCls} = this.props;
        const styleClass = classNames(
            prefixCls, className,
            'ApiContent',
        );
        const data = [{
            name: 'FabButton',
            fields: this.getFields(),
            attributes: [{
                attr: 'angle',
                desc: 'type"circle"',
                type: 'Number',
                default: '90',
            }, {
                attr: 'delay',
                desc: '',
                type: 'Number',
                default: '-',
            }, {
                attr: 'distance',
                desc: '',
                type: 'Number',
                default: '20',
            }, {
                attr: 'position',
                desc: 'top-left | bottom-left | top-right | bottom-right | center',
                type: 'String',
                default: 'bottom-right',
            }, {
                attr: 'icon',
                desc: '',
                type: 'React.ReactNode',
                default: '-',
            }, {
                attr: 'reverse',
                desc: '',
                type: 'Boolean',
                default: 'false',
            }, {
                attr: 'type',
                desc: 'horizontal | vertical | circle',
                type: 'String',
                default: 'horizontal',
            }],
        }];
        return (
            <Content className={styleClass}>
                <PageView
                    title="FabButton "
                    code={this.getCode()}
                    data={data}
                    app="WEB"
                    frameUrl="#/components/fab"
                    description=""
                />
            </Content>
        );
    }
}
