import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../../src/components/common/BaseProps';
import {Content} from '../../src/components/Page';
import PageView from '../components/PageView';

interface RadioApiProps extends BaseProps {
}

export default class RadioApi extends React.PureComponent<RadioApiProps, {}> {
    static defaultProps = {
        prefixCls: 'bm-RadioApi',
    };

    getCode() {
        return `import * as React from 'react';
import * as classNames from 'classnames';
import Radio from '../components/Radio';
import {BaseProps} from '../components/common/BaseProps';
import {Content} from '../components/Page';

interface Props extends BaseProps {}

export default class RadioCase extends React.PureComponent< Props, {}> {
    state = {
        value: ''
    };
    radios = [
        {
            value: 'Movie',
            label: 'Movie'
        }, {
            value: 'Food',
            label: 'Food'
        }, {
            value: 'Game',
            label: 'Game'
        }
    ];

    handleChange = (event: any) => {
        this.setState({
            value: event.value,
        });
    }
    render() {
        const {className} = this.props;
        const styleClass = classNames(
            'RadioCase',
            className
        );
        return (
            <Content className={styleClass}>
                <div className="CasePanel">
                    {
                        this.radios.map((radio, i) => {
                            return (
                                <Radio key={i} name="love" value={radio.value} checked={radio.value === this.state.value}
                                       onChange={this.handleChange.bind(this)}>
                                    {radio.label}
                                </Radio>
                            );
                        })
                    }
                    <div className="margin-top-10">
                        {this.state.value}
                    </div>
                </div>
            </Content>
        );
    }
}`;
    }

    getFields() {
        return [{
            field: 'attr',
            header: '',
        }, {
            field: 'desc',
            header: '',
        }, {
            field: 'type',
            header: '',
        }, {
            field: 'default',
            header: '',
        }];
    }

    render() {
        const {className, prefixCls} = this.props;
        const styleClass = classNames(
            prefixCls, className,
            'ApiContent',
        );
        const data = [{
            name: 'Radio',
            fields: this.getFields(),
            attributes: [{
                attr: 'checked',
                desc: '',
                type: 'Boolean',
                default: '-',
            }, {
                attr: 'defaultChecked',
                desc: '',
                type: 'Boolean',
                default: '-',
            }, {
                attr: 'disabled',
                desc: '',
                type: 'Boolean',
                default: 'false',
            }, {
                attr: 'name',
                desc: 'name',
                type: 'String',
                default: '-',
            }, {
                attr: 'onChange',
                desc: 'change ',
                type: '({checked, value}, event) => void',
                default: '-',
            }],
        }];
        return (
            <Content className={styleClass}>
                <PageView
                    title="Radio "
                    code={this.getCode()}
                    data={data}
                    app="WEB"
                    frameUrl="#/components/radio"
                    description=""
                />
            </Content>
        );
    }
}
