import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../../src/components/common/BaseProps';
import {Content} from '../../src/components/Page';
import PageView from '../components/PageView';

interface SliderApiProps extends BaseProps {
}

export default class SliderApi extends React.PureComponent<SliderApiProps, {}> {
    static defaultProps = {
        prefixCls: 'bm-SliderApi',
    };

    getCode() {
        return `import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../components/common/BaseProps';
import Slider from '../components/Slider';
import {Content} from '../components/Page';

interface SliderCaseProps extends BaseProps {
}

export default class SliderCase extends React.PureComponent< SliderCaseProps, any> {
    render() {
        const {className, ...other} = this.props;
        const styleClass = classNames(
            'SliderCase',
            className
        );
        return (
            <Content className={styleClass} {...other}>
                <section className="padding-20">
                    <Slider start="0" end="100"/>
                    <Slider className="padding-20" value={50} disabled={true}/>
                </section>
            </Content>
        );
    }
}`;
    }

    getFields() {
        return [{
            field: 'attr',
            header: '',
        }, {
            field: 'desc',
            header: '',
        }, {
            field: 'type',
            header: '',
        }, {
            field: 'default',
            header: '',
        }];
    }

    render() {
        const {className, prefixCls} = this.props;
        const styleClass = classNames(
            prefixCls, className,
            'ApiContent',
        );
        const data = [{
            name: 'Slider',
            fields: this.getFields(),
            attributes: [{
                attr: 'disabled',
                desc: '',
                type: 'Boolean',
                default: 'false',
            }, {
                attr: 'end',
                desc: '',
                type: 'React.ReactNode | string',
                default: '-',
            }, {
                attr: 'max',
                desc: '',
                type: 'Number',
                default: '100',
            }, {
                attr: 'min',
                desc: '',
                type: 'Number',
                default: '0',
            }, {
                attr: 'start',
                desc: '',
                type: 'React.ReactNode | string',
                default: '-',
            }, {
                attr: 'step',
                desc: '',
                type: 'Number',
                default: '1',
            }, {
                attr: 'vertical',
                desc: '',
                type: 'Boolean',
                default: 'false',
            }, {
                attr: 'value',
                desc: '',
                type: 'Number',
                default: '0',
            }, {
                attr: 'onChange',
                desc: '',
                type: '(index: number) => void',
                default: '-',
            }],
        }];
        return (
            <Content className={styleClass}>
                <PageView
                    title="Slider "
                    code={this.getCode()}
                    data={data}
                    app="WEB"
                    frameUrl="#/components/slider"
                    description=""
                />
            </Content>
        );
    }
}
