import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../../src/components/common/BaseProps';
import {Content} from '../../src/components/Page';
import PageView from '../components/PageView';

interface SwitchApiProps extends BaseProps {
}

export default class SwitchApi extends React.PureComponent<SwitchApiProps, {}> {
    static defaultProps = {
        prefixCls: 'bm-SwitchApi',
    };

    getCode() {
        return `import * as React from 'react';
import * as classNames from 'classnames';
import Switch from '../components/Switch';
import {BaseProps} from '../components/common/BaseProps';
import {Content} from '../components/Page';

interface Props extends BaseProps {}

export default class SwitchCase extends React.PureComponent< Props, {}> {
    render() {
        const {className} = this.props;
        const styleClass = classNames(
            'SwitchCase',
            className
        );
        return (
            <Content className={styleClass}>
                <div className="CasePanel">
                    <Switch>
                        Off
                    </Switch>
                    <Switch defaultChecked={true}>
                        On
                    </Switch>
                    <Switch disabled={true}>
                        Disabled off
                    </Switch>
                    <Switch disabled={true} defaultChecked={true}>
                        Disabled on
                    </Switch>
                </div>
            </Content>
        );
    }
}`;
    }

    getFields() {
        return [{
            field: 'attr',
            header: '',
        }, {
            field: 'desc',
            header: '',
        }, {
            field: 'type',
            header: '',
        }, {
            field: 'default',
            header: '',
        }];
    }

    render() {
        const {className, prefixCls} = this.props;
        const styleClass = classNames(
            prefixCls, className,
            'ApiContent',
        );
        const data = [{
            name: 'Switch',
            fields: this.getFields(),
            attributes: [{
                attr: 'checked',
                desc: '',
                type: 'Boolean',
                default: 'false',
            }, {
                attr: 'defaultChecked',
                desc: '',
                type: 'Boolean',
                default: 'false',
            }, {
                attr: 'disabled',
                desc: '',
                type: 'Boolean',
                default: 'false',
            }, {
                attr: 'name',
                desc: 'switchname',
                type: 'String',
                default: '-',
            }, {
                attr: 'onChange',
                desc: 'change ',
                type: '(checked: boolean): void',
                default: '-',
            }],
        }];
        return (
            <Content className={styleClass}>
                <PageView
                    title="Switch "
                    code={this.getCode()}
                    data={data}
                    app="WEB"
                    frameUrl="#/components/switch"
                    description=""
                />
            </Content>
        );
    }
}
