import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../../src/components/common/BaseProps';
import {Content} from '../../src/components/Page';
import PageView from '../components/PageView';
import MessageBox from '../../src/components/MessageBox';

interface MessageBoxApiProps extends BaseProps {
}

export default class MessageBoxApi extends React.PureComponent<MessageBoxApiProps, {}> {
    static defaultProps = {
        prefixCls: 'bm-MessageBoxApi',
    };

    getCode() {
        return `import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../components/common/BaseProps';
import Button from '../components/Button';
import MessageBox from '../components/MessageBox';
import Spin from '../components/Spin';
import Progress from '../components/Progress';
import {Content} from '../components/Page';

interface MessageBoxCaseProps extends BaseProps {
}

export default class MessageBoxCase extends React.PureComponent< MessageBoxCaseProps, any> {
    handleOpenAlert() {
        MessageBox.alert({
            title: '',
            message: 'Alert!'
        });
    }
    handleOpenConfirm() {
        MessageBox.confirm({
            title: '',
            message: 'Confirm!'
        });
    }
    handleOpenPrompt() {
        MessageBox.prompt({
            title: '',
            message: 'Prompt!'
        });
    }
    handleOpenPreloader = () => {
        MessageBox.show({
            message: (<div className="Row Row-column justify-center">
                <p>loading...</p>
                <Spin/>
            </div>),
            hideBackdrop: true
        })
    };
    handleOpenInfinite = () => {
        MessageBox.show({
            message: (<div className="Row Row-column justify-center">
                <p>loading...</p>
                <Progress loading={true}/>
            </div>),
            hideBackdrop: true
        })
    };
    render() {
        const styleClass = classNames(
            'MessageBoxCase'
        );
        return (
            <Content className={styleClass}>
                <div className="CasePanel">
                    <Button block onClick={this.handleOpenAlert.bind(this)}> alert </Button>
                    <Button block onClick={this.handleOpenConfirm.bind(this)}> confirm </Button>
                    <Button block onClick={this.handleOpenPrompt.bind(this)}> prompt </Button>
                </div>
                <div className="CasePanel">
                    <Button block onClick={this.handleOpenPreloader}>Preloader</Button>
                    <Button block onClick={this.handleOpenInfinite}>Infinite</Button>
                </div>
            </Content>
        );
    }
}`;
    }

    getFields() {
        return [{
            field: 'attr',
            header: '',
        }, {
            field: 'desc',
            header: '',
        }, {
            field: 'type',
            header: '',
        }, {
            field: 'default',
            header: '',
        }];
    }

    render() {
        const {className, prefixCls} = this.props;
        const styleClass = classNames(
            prefixCls, className,
            'ApiContent',
        );
        const data = [{
            name: 'MessageBox.show()',
            fields: this.getFields(),
            attributes: [{
                attr: 'delay',
                desc: '',
                type: 'Number',
                default: '0',
            }, {
                attr: 'hideBackdrop',
                desc: '',
                type: 'Boolean',
                default: 'false',
            }, {
                attr: 'inputType',
                desc: 'typepromptinputtype',
                type: 'String',
                default: 'text',
            }, {
                attr: 'message',
                desc: '',
                type: 'React.ReactNode | String',
                default: '-',
            }, {
                attr: 'placeholder',
                desc: 'inputplaceholder',
                type: 'String',
                default: '-',
            }, {
                attr: 'showConfirmButton',
                desc: '',
                type: 'Boolean',
                default: 'false',
            }, {
                attr: 'showCancelButton',
                desc: '',
                type: 'Boolean',
                default: 'false',
            }, {
                attr: 'title',
                desc: '',
                type: 'React.ReactNode | String',
                default: '-',
            }, {
                attr: 'type',
                desc: 'input',
                type: 'String',
                default: 'text',
            }, {
                attr: 'onClose',
                desc: '',
                type: 'Function',
                default: '-',
            }],
        }];
        return (
            <Content className={styleClass}>
                <PageView
                    title="MessageBox "
                    code={this.getCode()}
                    data={data}
                    app="WEB"
                    frameUrl="#/components/messageBox"
                    description=""
                />
                <div className="padding-left-20 padding-right-20">
                    <h3>MessageBox.alert()</h3>
                    <p>Alert</p>
                </div>
                <div className="padding-left-20 padding-right-20">
                    <h3>MessageBox.confirm()</h3>
                    <p>Confirm</p>
                </div>
                <div className="padding-left-20 padding-right-20">
                    <h3>MessageBox.prompt()</h3>
                    <p>Prompt</p>
                </div>
                <div className="padding-left-20 padding-right-20">
                    <h3>MessageBox.close()</h3>
                    <p>Close</p>
                </div>
            </Content>
        );
    }
}
