import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../../src/components/common/BaseProps';
import {Content} from '../../src/components/Page';
import PageView from '../components/PageView';
import SearchBar from '../../src/components/SearchBar/SearchBar';

interface SearchBarApiProps extends BaseProps {
}

export default class SearchBarApi extends React.PureComponent<SearchBarApiProps, {}> {
    static defaultProps = {
        prefixCls: 'bm-SearchBarApi',
    };

    getCode() {
        return `import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../components/common/BaseProps';
import SearchBar from '../components/SearchBar';
import {Content} from '../components/Page';

interface SearchBarCaseProps extends BaseProps {
}

export default class SearchBarCase extends React.PureComponent< SearchBarCaseProps, any> {
    render() {
        const {className, ...other} = this.props;
        const styleClass = classNames(
            'SearchBarCase',
            className
        );
        return (
            <Content className={styleClass} {...other}>
                <section className="CasePanel">
                    <SearchBar/>
                </section>
            </Content>
        );
    }
}`;
    }

    getFields() {
        return [{
            field: 'attr',
            header: '',
        }, {
            field: 'desc',
            header: '',
        }, {
            field: 'type',
            header: '',
        }, {
            field: 'default',
            header: '',
        }];
    }

    render() {
        const {className, prefixCls} = this.props;
        const styleClass = classNames(
            prefixCls, className,
            'ApiContent',
        );
        const data = [{
            name: 'SearchBar',
            fields: this.getFields(),
            attributes: [{
                attr: 'autoFocus',
                desc: '',
                type: 'Boolean',
                default: 'false',
            }, {
                attr: 'cancelText',
                desc: 'cancel',
                type: 'React.ReactNode',
                default: '',
            }, {
                attr: 'defaultValue',
                desc: '',
                type: 'String',
                default: '-',
            }, {
                attr: 'disabled',
                desc: '',
                type: 'Boolean',
                default: 'false',
            }, {
                attr: 'maxLength',
                desc: '',
                type: 'Number',
                default: '-',
            }, {
                attr: 'placeholder',
                desc: 'placeholder',
                type: 'String',
                default: '...',
            }, {
                attr: 'onCancel',
                desc: '',
                type: 'Function',
                default: '-',
            }, {
                attr: 'onChange',
                desc: 'change',
                type: '(value: string) => void',
                default: '-',
            }, {
                attr: 'onSubmit',
                desc: 'enter',
                type: '(value: string) => void',
                default: '-',
            }, {
                attr: 'onFocus',
                desc: 'focus',
                type: 'Function',
                default: '-',
            }, {
                attr: 'onBlur',
                desc: 'blur',
                type: 'Function',
                default: '-',
            }],
        }];
        return (
            <Content className={styleClass}>
                <PageView
                    title="SearchBar "
                    code={this.getCode()}
                    data={data}
                    app="WEB"
                    frameUrl="#/components/searchBar"
                    description=""
                />
            </Content>
        );
    }
}
