import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../../src/components/common/BaseProps';
import {Content} from '../../src/components/Page';
import PageView from '../components/PageView';

interface KnobApiProps extends BaseProps {
}

export default class KnobApi extends React.PureComponent<KnobApiProps, {}> {
    static defaultProps = {
        prefixCls: 'bm-KnobApi',
    };

    getCode() {
        return `import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../components/common/BaseProps';
import Knob from '../components/Knob';
import Button from '../components/Button';
import Icon from '../components/Icon';
import {Content} from '../components/Page';

interface KnobCaseProps extends BaseProps {
}

interface KnobCaseState {
    value: number
}

export default class KnobCase extends React.PureComponent< KnobCaseProps, KnobCaseState> {
    state = {
        value: 0
    };

    handleLoad = () => {
        let value = this.state.value;
        value += 10;
        if (value > 100) {
            value = 0;
        }
        this.setState({
            value
        });
    };

    render() {
        const {className} = this.props;
        const styleClass = classNames(
            'KnobCase',
            className
        );
        return (
            <Content className={styleClass}>
                <div className="CasePanel">
                    <Knob value={this.state.value}>
                        {this.state.value + '%'}
                    </Knob>
                    <Button theme="primary" onClick={this.handleLoad}>Add</Button>
                </div>
                <div className="CasePanel">
                    <Knob value={100} gapDegree={40}>
                        100%
                    </Knob>
                    <Knob value={50}>
                        <Icon icon="local_grocery_store"/>
                    </Knob>
                </div>
            </Content>
        );
    }
}`;
    }

    getFields() {
        return [{
            field: 'attr',
            header: '',
        }, {
            field: 'desc',
            header: '',
        }, {
            field: 'type',
            header: '',
        }, {
            field: 'default',
            header: '',
        }];
    }

    render() {
        const {className, prefixCls} = this.props;
        const styleClass = classNames(
            prefixCls, className,
            'ApiContent',
        );
        const data = [{
            name: 'Calendar',
            fields: this.getFields(),
            attributes: [{
                attr: 'animated',
                desc: '',
                type: 'Boolean',
                default: 'true',
            }, {
                attr: 'barColor',
                desc: '',
                type: 'String',
                default: '#5db2ff',
            }, {
                attr: 'lineWidth',
                desc: '',
                type: 'number',
                default: '6',
            }, {
                attr: 'size',
                desc: '',
                type: 'number',
                default: '100',
            }, {
                attr: 'trackColor',
                desc: '',
                type: 'number',
                default: '#eee',
            }, {
                attr: 'value',
                desc: '',
                type: 'number',
                default: '0',
            }],
        }];
        return (
            <Content className={styleClass}>
                <PageView
                    title="Knob "
                    code={this.getCode()}
                    data={data}
                    app="WEB"
                    frameUrl="#/components/knob"
                    description=""
                />
            </Content>
        );
    }
}
