import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../../src/components/common/BaseProps';
import {Content} from '../../src/components/Page';
import PageView from '../components/PageView';

interface NotificationApiProps extends BaseProps {
}

export default class NotificationApi extends React.PureComponent<NotificationApiProps, {}> {
    static defaultProps = {
        prefixCls: 'bm-NotificationApi',
    };

    getCode() {
        return `import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../components/common/BaseProps';
import Notification from '../components/Notification';
import Button from '../components/Button';
import Avatar from '../components/Avatar';
import {Content} from '../components/Page';

interface NotificationCaseProps extends BaseProps {
}

export default class NotificationCase extends React.PureComponent< NotificationCaseProps, any> {
    handleClick = () => {
        Notification.info({
            avatar: (
                <Avatar src="assets/images/users/1.jpg" className="SmAvatar"/>),
            title: 'Tip',
            message: ''
        });
    };

    render() {
        const {className, ...other} = this.props;
        const styleClass = classNames(
            'NotificationCase',
            className
        );
        return (
            <Content className={styleClass} {...other}>
                <div className="CasePanel">
                    <Button onClick={this.handleClick}> Notification</Button>
                </div>
            </Content>
        );
    }
}`;
    }

    getFields() {
        return [{
            field: 'attr',
            header: '',
        }, {
            field: 'desc',
            header: '',
        }, {
            field: 'type',
            header: '',
        }, {
            field: 'default',
            header: '',
        }];
    }

    render() {
        const {className, prefixCls} = this.props;
        const styleClass = classNames(
            prefixCls, className,
            'ApiContent',
        );
        const data = [{
            name: 'Notification',
            fields: this.getFields(),
            attributes: [{
                attr: 'avatar',
                desc: '',
                type: 'React.ReactNode',
                default: '-',
            }, {
                attr: 'delay',
                desc: '0',
                type: 'Number',
                default: '2500',
            }, {
                attr: 'message',
                desc: '',
                type: 'React.ReactNode | string',
                default: '-',
            }, {
                attr: 'position',
                desc: ' top | bottom',
                type: 'String',
                default: 'top',
            }, {
                attr: 'type',
                desc: 'default | info | success | warning | error',
                type: 'String',
                default: '-',
            }, {
                attr: 'onClose',
                desc: '',
                type: 'Function',
                default: '-',
            }],
        }];
        return (
            <Content className={styleClass}>
                <PageView
                    title="Notification "
                    code={this.getCode()}
                    data={data}
                    app="WEB"
                    frameUrl="#/components/notification"
                    description=""
                />
            </Content>
        );
    }
}
