import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../../src/components/common/BaseProps';
import {Content} from '../../src/components/Page';
import PageView from '../components/PageView';
import HighlightCode from '../../src/components/HighlightCode/HighlightCode';

interface PickerApiProps extends BaseProps {
}

export default class PickerApi extends React.PureComponent<PickerApiProps, {}> {
    static defaultProps = {
        prefixCls: 'bm-PickerApi',
    };

    getCode() {
        return `import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../components/common/BaseProps';
import Picker from '../components/Picker';
import {city} from './common/City';
import {Content} from '../components/Page';

interface PickerCaseProps extends BaseProps {
}

export default class PickerCase extends React.PureComponent< PickerCaseProps, {}> {

    handleChange = (event: any) => {
        console.log(event);
    };

    render() {
        const {className, ...other} = this.props;
        const styleClass = classNames(
            'PickerCase', className
        );
        return (
            <Content className={styleClass} {...other}>
                <div className="CasePanel">
                    <Picker data={city} onChange={this.handleChange}/>
                </div>
            </Content>
        );
    }
}`;
    }

    getFields() {
        return [{
            field: 'attr',
            header: '',
        }, {
            field: 'desc',
            header: '',
        }, {
            field: 'type',
            header: '',
        }, {
            field: 'default',
            header: '',
        }];
    }

    render() {
        const {className, prefixCls} = this.props;
        const styleClass = classNames(
            prefixCls, className,
            'ApiContent',
        );
        const data = [{
            name: 'Picker',
            fields: this.getFields(),
            attributes: [{
                attr: 'disabled',
                desc: '',
                type: 'Boolean',
                default: 'false',
            }, {
                attr: 'data',
                desc: '',
                type: 'Array< MPickerChild>',
                default: '-',
            }, {
                attr: 'format',
                desc: 'value',
                type: 'String',
                default: '/',
            }, {
                attr: 'onChange',
                desc: '',
                type: '({event, value, data}) => void',
                default: '-',
            }, {
                attr: 'onValueChange',
                desc: '',
                type: '({value, index}) => void',
                default: '-',
            }, {
                attr: 'value',
                desc: '[value1, value2, value3], valuevalue1/value2/value3',
                type: 'Array< any> | string',
                default: '-',
            }, {
                attr: 'visible',
                desc: '',
                type: 'Boolean',
                default: 'false',
            }],
        }];
        const code = `export interface PickerNode {
    label?: string;
    value?: any;
    children?: any[]
}`;
        return (
            <Content className={styleClass}>
                <PageView
                    title="Picker "
                    code={this.getCode()}
                    data={data}
                    app="WEB"
                    frameUrl="#/components/Picker"
                    description=""
                />
                <div className="padding-left-20 padding-right-20">
                    <h3>PickerNode</h3>
                    <p></p>
                    <HighlightCode lang="typescript">
                        {code}
                    </HighlightCode>
                </div>
            </Content>
        );
    }
}
