import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../../src/components/common/BaseProps';
import {Content} from '../../src/components/Page';
import PageView from '../components/PageView';

interface PaginationApiProps extends BaseProps {
}

export default class PaginationApi extends React.PureComponent<PaginationApiProps, {}> {
    static defaultProps = {
        prefixCls: 'bm-PaginationApi',
    };

    getCode() {
        return `import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../components/common/BaseProps';
import Pagination from '../components/Pagination';
import {Content} from '../components/Page';

interface PaginationCaseProps extends BaseProps {
}

export default class PaginationCase extends React.PureComponent< PaginationCaseProps, any> {
    render() {
        const {className, ...other} = this.props;
        const styleClass = classNames(
            'PaginationCase',
            className
        );
        return (
            <Content className={styleClass} {...other}>
                <div className="CasePanel">
                    <Pagination total={50} pageSize={10}/>
                </div>
                <div className="CasePanel">
                    <Pagination shape="circle" total={50} pageSize={10}/>
                </div>
            </Content>
        );
    }
}`;
    }

    getFields() {
        return [{
            field: 'attr',
            header: '',
        }, {
            field: 'desc',
            header: '',
        }, {
            field: 'type',
            header: '',
        }, {
            field: 'default',
            header: '',
        }];
    }

    render() {
        const {className, prefixCls} = this.props;
        const styleClass = classNames(
            prefixCls, className,
            'ApiContent',
        );
        const data = [{
            name: 'Pagination',
            fields: this.getFields(),
            attributes: [{
                attr: 'maxPage',
                desc: '',
                type: 'number',
                default: '5',
            }, {
                attr: 'pageSize',
                desc: '',
                type: 'number',
                default: '10',
            }, {
                attr: 'shape',
                desc: '"circle"',
                type: 'String',
                default: '-',
            }, {
                attr: 'total',
                desc: '',
                type: 'number',
                default: '-',
            }, {
                attr: 'value',
                desc: '',
                type: 'number',
                default: '1',
            }, {
                attr: 'onPageChange',
                desc: '',
                type: '(value) => void',
                default: '-',
            }],
        }];
        return (
            <Content className={styleClass}>
                <PageView
                    title="Pagination "
                    code={this.getCode()}
                    data={data}
                    app="WEB"
                    frameUrl="#/components/pagination"
                    description=""
                />
            </Content>
        );
    }
}
