import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../../src/components/common/BaseProps';
import {Content} from '../../src/components/Page';
import PageView from '../components/PageView';

interface CalendarApiProps extends BaseProps {
}

export default class CalendarApi extends React.PureComponent<CalendarApiProps, {}> {
    static defaultProps = {
        prefixCls: 'bm-CalendarApi',
    };

    getCode() {
        return `import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../components/common/BaseProps';
import Calendar from '../components/Calendar';
import {Content} from '../components/Page';

interface CalendarCaseProps extends BaseProps {
}

export default class CalendarCase extends React.PureComponent< CalendarCaseProps, {}> {
    state = {
        visible: false
    };

    handleChange = (value: any) => {
        console.log(value);
    };

    render() {
        const {className, ...other} = this.props;
        const styleClass = classNames(
            'CalendarCase',
            className
        );
        return (
            <Content className={styleClass} {...other}>
                <div className="CasePanel">
                    <Calendar onChange={this.handleChange}/>
                </div>
            </Content>
        );
    }
}`;
    }

    getFields() {
        return [{
            field: 'attr',
            header: '',
        }, {
            field: 'desc',
            header: '',
        }, {
            field: 'type',
            header: '',
        }, {
            field: 'default',
            header: '',
        }];
    }

    render() {
        const {className, prefixCls} = this.props;
        const styleClass = classNames(
            prefixCls, className,
            'ApiContent',
        );
        const data = [{
            name: 'Calendar',
            fields: this.getFields(),
            attributes: [{
                attr: 'defaultValue',
                desc: '',
                type: 'String',
                default: '-',
            }, {
                attr: 'format',
                desc: '',
                type: 'String',
                default: 'yyyy-MM-dd',
            }, {
                attr: 'minDate',
                desc: '',
                type: 'String',
                default: '-',
            }, {
                attr: 'maxDate',
                desc: '',
                type: 'String',
                default: '-',
            }, {
                attr: 'visible',
                desc: '',
                type: 'Boolean',
                default: 'false',
            }, {
                attr: 'onChange',
                desc: '',
                type: '(value: string): void',
                default: '-',
            }],
        }];
        return (
            <Content className={styleClass}>
                <PageView
                    title="Calendar "
                    code={this.getCode()}
                    data={data}
                    app="WEB"
                    frameUrl="#/components/calendar"
                    description=""
                />
            </Content>
        );
    }
}
