import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../../src/components/common/BaseProps';
import {Content} from '../../src/components/Page';
import PageView from '../components/PageView';

interface TextareaApiProps extends BaseProps {
}

export default class TextareaApi extends React.PureComponent<TextareaApiProps, {}> {
    static defaultProps = {
        prefixCls: 'bm-TextareaApi',
    };

    getCode() {
        return `import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../components/common/BaseProps';
import Textarea from '../components/Textarea';
import {Content} from '../components/Page';

interface TextareaCaseProps extends BaseProps {
}

export default class TextareaCase extends React.PureComponent< TextareaCaseProps, {}> {
    static defaultProps = {
        prefixCls: 'bm-TextareaCase'
    };

    render() {
        const {className, prefixCls} = this.props;
        const styleClass = classNames(
            prefixCls, className
        );
        return (
            <Content className={styleClass}>
                <div className="padding-20">
                    <Textarea placeholder=""/>
                </div>
            </Content>
        );
    }
}
`;
    }

    getFields() {
        return [{
            field: 'attr',
            header: '',
        }, {
            field: 'desc',
            header: '',
        }, {
            field: 'type',
            header: '',
        }, {
            field: 'default',
            header: '',
        }];
    }

    render() {
        const {className, prefixCls} = this.props;
        const styleClass = classNames(
            prefixCls, className,
            'ApiContent',
        );
        const data = [{
            name: 'Textarea',
            fields: this.getFields(),
            attributes: [{
                attr: 'defaultValue',
                desc: '',
                type: 'String',
                default: '-',
            }, {
                attr: 'disabled',
                desc: '',
                type: 'Boolean',
                default: 'false',
            }, {
                attr: 'label',
                desc: '',
                type: 'React.ReactNode | string',
                default: '-',
            }, {
                attr: 'maxLength',
                desc: '',
                type: 'Number',
                default: '-',
            }, {
                attr: 'maxHeight',
                desc: '',
                type: 'Number',
                default: '-',
            }, {
                attr: 'placeholder',
                desc: '',
                type: 'String',
                default: '-',
            }, {
                attr: 'readOnly',
                desc: '',
                type: 'Boolean',
                default: 'false',
            }, {
                attr: 'value',
                desc: '',
                type: 'String',
                default: '-',
            }, {
                attr: 'onChange',
                desc: 'change',
                type: '(value: string, event: any) => {}',
                default: '-',
            }],
        }];
        return (
            <Content className={styleClass}>
                <PageView
                    title="Textarea "
                    code={this.getCode()}
                    data={data}
                    app="WEB"
                    frameUrl="#/components/textarea"
                    description=""
                />
            </Content>
        );
    }
}
