import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../../src/components/common/BaseProps';
import {Content} from '../../src/components/Page';
import PageView from '../components/PageView';

interface RateApiProps extends BaseProps {
}

export default class RateApi extends React.PureComponent<RateApiProps, {}> {
    static defaultProps = {
        prefixCls: 'bm-RateApi',
    };

    getCode() {
        return `import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps} from '../components/common/BaseProps';
import Rate from '../components/Rate';
import {Content} from '../components/Page';

interface RateCaseProps extends BaseProps {
}

export default class RateCase extends React.PureComponent< RateCaseProps, any> {
    render() {
        const {className, ...other} = this.props;
        const styleClass = classNames(
            'RateCase',
            className
        );
        return (
            <Content className={styleClass} {...other}>
                <div className="CasePanel">
                    <Rate/>
                </div>
                <div className="CasePanel">
                    <Rate allowHalf={true} defaultValue={3}/>
                </div>
                <div className="CasePanel">
                    <Rate allowHalf={true} template=""/>
                </div>
            </Content>
        );
    }
}`;
    }

    getFields() {
        return [{
            field: 'attr',
            header: '',
        }, {
            field: 'desc',
            header: '',
        }, {
            field: 'type',
            header: '',
        }, {
            field: 'default',
            header: '',
        }];
    }

    render() {
        const {className, prefixCls} = this.props;
        const styleClass = classNames(
            prefixCls, className,
            'ApiContent',
        );
        const data = [{
            name: 'Rate',
            fields: this.getFields(),
            attributes: [{
                attr: 'allowHalf',
                desc: '',
                type: 'Boolean',
                default: 'false',
            }, {
                attr: 'count',
                desc: 'star ',
                type: 'Number',
                default: '5',
            }, {
                attr: 'defaultValue',
                desc: '',
                type: 'Number',
                default: '0',
            }, {
                attr: 'onChange',
                desc: '',
                type: '(value) => void',
                default: '-',
            }, {
                attr: 'readOnly',
                desc: '',
                type: 'Boolean',
                default: 'false',
            }, {
                attr: 'template',
                desc: '',
                type: 'React.ReactNode | String',
                default: '-',
            }, {
                attr: 'value',
                desc: '',
                type: 'Number',
                default: '-',
            }],
        }];
        return (
            <Content className={styleClass}>
                <PageView
                    title="Rate "
                    code={this.getCode()}
                    data={data}
                    app="WEB"
                    frameUrl="#/components/rate"
                    description=""
                />
            </Content>
        );
    }
}
